import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Icon from '@material-ui/core/Icon';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Button from '@material-ui/core/Button';
import { Bounce, Spinner } from 'react-activity';
import 'react-activity/dist/react-activity.css';


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  appBarIcon: {
    display: 'flex',
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1
  },
  container: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    paddingTop: 50,
    paddingBottom: 50
  },
  stepTitle: {
    width: '30%',
    marginTop: 20,
    marginBottom: 10
  },
  img: {
    marginTop: 10
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  placeholder_img_overlay: {
    position: 'absolute',
    left: 0, right: 0, top: 0, bottom: 0,
    backgroundColor: '#00000080',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  },
  label_box_container: {
    position: 'absolute',
    left: 0, right: 0, top: 0, bottom: 0
  },
  label_box: {
    borderStyle: 'dashed',
    borderWidth: 2
  },
  label: {
    position: 'absolute',
    left: -2,
    top: -24,
    lineHeight: '14px',
    padding: '5px',
    whiteSpace: 'nowrap'
  }
}));

const SERVER_URL = "";

function App() {
  const classes = useStyles();
  const [step, setStep] = React.useState(1);
  const [file, setFile] = React.useState();
  const [fileURL, setFileURL] = React.useState();
  const [isUploading, setUploading] = React.useState(false);
  const [darkLabelsEnabled, setDarkLabelsEnabled] = React.useState(false);
  const [result, setResult] = React.useState();
  let fileInputRef;

  const handleFileChange = (e) => {
    let f = e.target.files[0];
    setFile(f);
    setUploading(false);
    setResult(false);
    if (fileURL !== "")
      URL.revokeObjectURL(fileURL);

    setFileURL(f ? URL.createObjectURL(f) : "");
    setStep(f ? 2 : 1);
  };

  const handleSubmit = () => {
    if (!isUploading) {
      // Upload image to server
      setUploading(true);
      let data = new FormData();
      data.append('image', file);
      fetch(SERVER_URL + "/api/detect", { method: 'POST', body: data })
        .then(response => response.json())
        .then(response => {
          setStep(3);
          setUploading(false);
          // Start polling every 5s for result
          let interval = setInterval(async () => {
            try {
              let result = await fetch(`${SERVER_URL}/api/results?job_id=${response.job_id}`).then(r => r.json());
              if (result.completed) {
                setResult(result);
                clearInterval(interval);
              }
            }
            catch (err) {
              alert('Something went wrong. Please try again later.');
              clearInterval(interval);
              setStep(2);
            }
          }, 5000)
        })
        .catch(() => {
          alert("Couldn't connect to server. Make sure the server is running and try again.")
          setUploading(false);
        });
    }
  };

  const startOver = () => {
    fileInputRef.value = null;
    handleFileChange({ target: fileInputRef });
  };

  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Toolbar>
          <div className={classes.appBarIcon}>
            <Icon>blur_on</Icon>
          </div>
          <Typography variant="h6" className={classes.title}>
            AI - Multiple Object Recognition
          </Typography>
          <div style={{ marginLeft: 'auto' }}>
            <Typography variant="h6" style={{ opacity: 0.8 }}>
              {'By Joseph Edusei & Ofoe Agbettor'}
            </Typography>
          </div>
        </Toolbar>
      </AppBar>
      <div className={classes.container}>
        <Typography variant="h6" className={classes.stepTitle}>
          1. Select Image
        </Typography>
        <input type='file' accept='image/*' onChange={handleFileChange} disabled={step > 2} ref={ref => fileInputRef = ref} />
        {file &&
          <>
            <img src={fileURL} alt="" className={classes.img} />
            <Typography variant="h6" className={classes.stepTitle}>
              2. Upload Image
            </Typography>
          </>
        }
        {((step === 2 && !isUploading) || (step > 2)) &&
          <Button variant="contained" color="primary" disabled={step !== 2} onClick={handleSubmit}>
            Upload
          </Button>
        }
        {file && isUploading && !result &&
          <Button variant="contained" color="primary" disabled>
            <span> <Bounce size={15} color="#fff" /></span>
          </Button>
        }
        {step === 3 &&
          <Typography variant="h6" className={classes.stepTitle}>
            3. View Result
          </Typography>
        }
        {step === 3 && (() =>
          <>
            <FormControl className={classes.formControl}>
              <InputLabel id="color-label">Label color</InputLabel>
              <Select
                labelId="color-label"
                id="label-select"
                value={darkLabelsEnabled}
                onChange={(event) => setDarkLabelsEnabled(event.target.value)}
              >
                <MenuItem value={false}>Light</MenuItem>
                <MenuItem value={true}>Dark</MenuItem>
              </Select>
            </FormControl>
            <div className={classes.img} style={{ position: 'relative' }}>
              <img src={fileURL} alt="" />
              {!result ?
                <div className={classes.placeholder_img_overlay}>
                  <Spinner size={20} color='#fff' />
                </div>
                : [
                  <div key={0} className={classes.label_box_container}>
                    {result.labels.map((l, i) => (result.scores[i] > 0.4) &&
                      <LabelledBox key={String(i)} labelClassName={classes.label} boxClassName={classes.label_box} darkMode={darkLabelsEnabled} label={`${l} (${(result.scores[i] * 100).toFixed(2)}%)`} coords={result.boxes[i]} />
                    )}
                  </div>
                ]
              }
            </div>
          </>
        )()}
        {step === 3 &&
          <div style={{ marginTop: 30 }}>
            <Button variant="contained" color="primary" onClick={startOver}>
              Start Over
            </Button>
          </div>
        }
      </div>
    </div>
  );
}

function LabelledBox({ label, coords, labelClassName, boxClassName, darkMode }) {
  return (
    <div className={boxClassName} style={{ borderColor: (darkMode ? '#000000cc' : '#ffffffcc'), position: 'absolute', left: coords[0], top: coords[1], width: coords[2] - coords[0], height: coords[3] - coords[1] }}>
      <span className={labelClassName} style={{ backgroundColor: (darkMode ? '#000000cc' : '#ffffffcc'), color: (darkMode ? '#fff' : '#000') }}>{label}</span>
    </div>
  );
}

export default App;